var _self;		
	export default {
		data() {
			return {
				imageSrc: '',
				testuser:[]
			}
		},
		onLoad:function(){
		    _self = this;
		},
		methods: {
			//增加的自己的函数
			addtest(){
				uni.showLoading({title: '显示处理中...'})
				myCloud.callFunction({
					name: 'fengtest',
					data: {
						name: '张三',
						subType: '12',
						createTime: Date.now()
					}
				}).then((res) => {
					uni.hideLoading()
					uni.showToast({
					    title: '成功添加一条数据，文档id为：${res.result.id}',
					    duration: 1000
					});
					console.log(res)
				}).catch((err) => {
					uni.hideLoading()
					uni.showModal({
						content: `失败，错误信息为：${err.message}`,
						showCancel: false
					})
					console.error(err)
				})
				},
		gettest(){
			uni.showLoading({title: '获取中...'})
			myCloud.callFunction({
				name: 'gettest',
				data: {
					userid: '1123424556',
					createTime: Date.now()
				}
			}).then((res)=>{
				uni.hideLoading();
				_self.testuser = res.result.data;
				//获了成功
				uni.showModal({
					content: ` 成功` + JSON.stringify(res.result.data),
					showCancel: false
				})
			}).catch((err) => {
					uni.hideLoading()
					uni.showModal({content: `失败，错误信息为：${err.message}`,showCancel: false})
				})
		},
		//进入主页
		inindex(){
			uni.switchTab({
			    url: '/pages/mainindex/mainindex'
			});
		}
		
				
		
			
		
